<template>
  <div class="auth full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="auth-content">
          <div 
            class="auth-wrapper"
            v-for="(item, index) in list" 
            :key="index">
            <div class="auth-logo" :class="(isfirst || item.selected) ? `auth-logo-${item.bgimg}` : `auth-logo-${item.bgimg}-off`" @click="selectRole(index, item)"></div>
            <div class="auth-inner"  @click="selectRole(index, item)">
              <div class="auth-title">{{ item.title }}</div>
              <div class="auth-desc">{{ item.desc }}</div>
              <div class="auth-caption" v-if="item.caption">{{ item.caption }}</div>
              <div class="auth-agree" v-if="item.selected && index !== 2">
                <span class="auth-agree-radio" @click.stop="switchRadio(item)" :class="item.isagree ? 'on' : 'off'"></span>
                <span class="auth-agree-txt" @click.stop="switchRadio(item)">我已同意</span>
                <router-link tag="span" class="auth-protocol" to="/agree">
                  《{{item.agreetxt}}》
                </router-link>
              </div>
              <div class="auth-btn"  v-if="item.selected && index !== 2" @click.stop="enter(item)">确定</div>
            </div>
          </div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-toast ref="toast" :text="toastText" :class="(toastWrap ? 'toast-wrap' : '')"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { mapState } from 'vuex'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemScrollView,
    PemToast
  },
  data () {
    return {
      isfirst: true,
      toastWrap: false,
      toastText: '',
      list: [{
        title: '投资人',
        agreetxt: '投资人协议',
        desc: '适用于天使投资人及专业的机构投资者',
        caption: '优质项目及时推送  全网数据自由检索',
        to: '/auth-investor-one',
        selected: false,
        isagree: true,
        bgimg: 'investor'
      }, {
        title: '创业者',
        agreetxt: '创业者协议',
        desc: '适用于正在或即将寻找融资的创业者',
        caption: '免费发布融资项目  定向推荐投资人',
        to: '/auth-entrepreneur',
        selected: false,
        isagree: true,
        bgimg: 'entrepreneur'
      }, {
        title: '财务顾问',
        agreetxt: '财务顾问协议',
        desc: '适用于从事FA (财务顾问) 的个人用户',
        caption: '财务顾问身份暂未开放',
        to: '',
        selected: false,
        isagree: true,
        bgimg: 'fa'
      }, {
        title: '游客身份',
        agreetxt: '用户协议',
        desc: '对游客开放了部分访问权限，快去体验吧',
        caption: '',
        to: '/auth-yk',
        selected: false,
        isagree: true,
        bgimg: 'yk'
      }]
    }
  },
  computed: mapState({auth: state => state.auth}),
  created () {
    this._initWXShare()
    this._setTitle('项目工场-认证')
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    selectRole (roleIdx) {
      if (roleIdx === 2) {
        this.toastWrap = true
        this.toastText = '财务顾问暂未开放,可登录 【项目工场】 App认证'
        this.$refs.toast.show()
        return
      }
      if (roleIdx === 3 && this.auth && this.auth.type === 'syk') {
        this.toastWrap = true
        this.toastText = '您已经是游客身份'
        this.$refs.toast.show()
        return
      }
      if (this.isfirst) this.isfirst = false
      for (let i = 0, len = this.list.length; i < len; i++) {
        this.list[i]['selected'] = roleIdx === i
        if (roleIdx !== i) this.list[i]['isagree'] = true
      }
    },
    switchRadio (item) {
      item.isagree = !item.isagree
    },
    enter (item) {
      if (!item.isagree) {
        this.toastWrap = false
        this.toastText = `您还未同意${item.agreetxt}`
        this.$refs.toast.show()
        return
      }
      this.$router.push(item.to)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
  .toast-wrap
    white-space: inherit
    padding: 0.3rem 0.1rem
    width: 6.2rem
    max-width: 6.2rem
    box-sizing: border-box
  .auth-content
    padding-bottom: 1.5rem
  .auth-wrapper
    position: relative
    padding: 1.3rem 0.3rem 0
    .auth-logo
      display: inline-block
      position: absolute 
      top: 0.26rem
      left: 50%
      margin-left: -0.64rem
      width: 1.28rem
      height: 1.04rem
      background-size: 100% 100%
      background-repeat: no-repeat
    .auth-logo-investor
      bg-image('../../../common/image/minisite/auth/investor')
    .auth-logo-investor-off
      bg-image('../../../common/image/minisite/auth/investor_off')
    .auth-logo-entrepreneur
      bg-image('../../../common/image/minisite/auth/entrepreneur')
    .auth-logo-entrepreneur-off
      bg-image('../../../common/image/minisite/auth/entrepreneur_off')
    .auth-logo-fa
      bg-image('../../../common/image/minisite/auth/fa')
    .auth-logo-fa-off
      bg-image('../../../common/image/minisite/auth/fa_off')
    .auth-logo-yk
      bg-image('../../../common/image/minisite/auth/yk')
    .auth-logo-yk-off
      bg-image('../../../common/image/minisite/auth/yk_off')
  .auth-inner
    padding: 0.3rem 0 
    background: #F6F6F6
    border-radius: 0.08rem
    text-align: center
    .auth-title
      height: 0.56rem
      line-height: 0.56rem
      font-size: 0.4rem
      font-weight: bold
      color: #41403E
    .auth-desc
      height: 0.4rem
      line-height: 0.4rem
      font-size: 0.28rem
      color: #41403E
      padding-top: 0.2rem
    .auth-caption
      height: 0.34rem
      line-height: 0.34rem
      font-size: 0.24rem
      color: #A7A5A3
      padding-top: 0.1rem
    .auth-agree
      height: 0.34rem
      line-height: 0.34rem
      font-size: 0.24rem
      color: #535251
      padding-top: 0.22rem
      span
        display: inline-block
        vertical-align: middle
      .auth-agree-radio
        width: 0.24rem
        height: 0.24rem
        border-radius: 100%
        background-size: 100% 100%
        background-repeat: no-repeat
        &.on
          bg-image('../../../common/image/minisite/auth/icon-Agreement-selected')
        &.off
          bg-image('../../../common/image/minisite/auth/icon-Agreement-Unselected')
      .auth-protocol
        color: #ff8f41
    .auth-btn
      width: 1.84rem
      height: 0.6rem
      margin: 0 auto
      line-height: 0.6rem
      background: #FFC134
      border-radius: 0.04rem
      font-size: 0.28rem
      color: #41403E
      margin-top: 0.3rem
</style>



